import React from 'react';
import vector from 'assets/work/vacancy_vector.png';
import { Button } from 'ui/Button';
import { Link } from 'react-router-dom';

export function SuccessScreen() {
  return (
    <div className="success">
      <div className="success__title">
        <img src={vector} alt="" />
        <span>Ваш отклик был успешно отправлен!</span>
      </div>

      <p className="success__text">
        В ближайшее время с вами свяжется отдел по работе с персоналом по телефону или придет ответ
        с результатами на почту
      </p>

      <div className="success__feed">
        <p>По возникшим вопросам, обращайтесь:</p>
        <p>
          Отдел по работе с персоналом: <span>+7 (929) 128-16-76</span>
        </p>
        <p>
          Электронная почта: <span>hr@avanta-service.com</span>
        </p>
        <Link to={'/'}>
          <Button>Вернуться на главную страницу</Button>
        </Link>
      </div>
    </div>
  );
}
