import React from 'react';
import { Header } from 'components/Header';
import { TextField } from '../../../ui/TextField';
import header_banner from 'assets/work/image43.png';
import axios from 'axios';
import { Button } from 'ui/Button';
import { useParams } from 'react-router';
import { SuccessScreen } from '.';

export const workHaeader = {
  title: 'Отклик на прохождение практики',
  img: header_banner,
};

export function ResponseStudents() {
  const [values, setValues] = React.useState({
    name: '',
    phone: '',
    email: '',
    disc: '',
  });
  const [isSuccess, setIsSuccess] = React.useState(false);

  const { id } = useParams();
  console.log(id);
  async function handleSubmit(e) {
    e.preventDefault();

    try {
      const response = await axios({
        url: 'http://91.200.84.190/api/work/user_vacancy/',
        method: 'POST',
        data: {
          name: values.name,
          number: values.phone,
          mail: values.email,
          course_of_study: values.disc,
          vacancy: id,
        },
      });
      console.log(response);
      setIsSuccess(true);
    } catch (err) {
      alert(`Произошла ошибка${err}`);
    }
  }
  return (
    <>
      <Header {...workHaeader} whiteBackground />

      <main>
        <div className="container">
          {isSuccess ? (
            <SuccessScreen />
          ) : (
            <>
              <section className="response-stud">
                <div className="smalls__fields">
                  <TextField
                    value={values.name}
                    type={'text'}
                    onChange={(e) => setValues((prev) => ({ ...prev, name: e.target.value }))}
                    placeholder={'Иван'}
                    label={'Имя*'}
                    small
                  />
                  <TextField
                    value={values.phone}
                    onChange={(e) => setValues((prev) => ({ ...prev, phone: e.target.value }))}
                    type={'phone'}
                    placeholder={'+7 (___) ___-__-__'}
                    label={'Телефон*'}
                    small
                  />
                </div>

                <div>
                  <TextField
                    value={values.email}
                    type={'email'}
                    onChange={(e) => setValues((prev) => ({ ...prev, email: e.target.value }))}
                    placeholder={'Ivan1888@mail.ru'}
                    label={'Почта*'}
                    fullWidth
                  />
                  <TextField
                    value={values.disc}
                    onChange={(e) => setValues((prev) => ({ ...prev, disc: e.target.value }))}
                    type={'text'}
                    placeholder={'Инженер-механик, 5-й курс'}
                    label={'Специализация, курс обучения*'}
                    fullWidth
                  />
                </div>

                <div>
                  <p className="fields__disc">
                    Нажимая на кнопку "Отправить", вы подтверждаете что даёте согласие на обработку
                    персональных данных в соответствии с условиями политики
                  </p>

                  <Button onClick={handleSubmit}>Отправить</Button>
                </div>
              </section>
            </>
          )}
        </div>
      </main>
    </>
  );
}
